import Choices from "choices.js";

if (document.querySelector('.products__sort')) {
    const defaultSelect = () => {
        const element = document.querySelector('.products__sort');
        const choices = new Choices(element, {
            searchEnabled: false,
            sorter: () => {},
        });
    }

    defaultSelect()
}

